s;\r\n\t\ttransition: font-size .1s;\r\n\t}\r\n\r\n\t.tools-close > div > a {\r\n\t\twidth: 10%;\r\n\t\tfont-size: 0pt;\r\n\t\tmargin-top: 5px;\r\n\t\tmargin-bottom: 5px;\r\n\t\tleft: auto;\r\n\t\tright: auto;\r\n\r\n\t\ttransition: width .5s;\r\n\t\ttransition: font-size .1s;\r\n\t}\r\n</style>"],"sourceRoot":""}]);
